@import url(https://fonts.googleapis.com/css?family=Aleo|Fredoka+One);
.game.title{
    white-space: nowrap;
}

.computer{
    font-size: 45px;
    font-family: 'Aleo', serif;
    display: inline-block;
    color: #FF0000;
}
.player{
    font-size: 45px;
    font-family: 'Aleo', serif;
    display: inline-block;
    color: #5AA5E8;
}
#TTTGrid{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    margin-top: 25px;
    display: grid;
    grid-template-rows: repeat(3, minmax(50px,200px));
    grid-template-columns: repeat(3, minmax(50px,200px));
}
.square{
    width: 100%;
    height: 100%;
    border: 1px solid black;
    background-repeat: no-repeat;
    background-size: 80% 80%;
    background-image: url(/quezGames/static/media/x.5e6b6fb4.png);
    background-position: center;
}
#score-container{
    width: 400px;
    margin: 0 auto;
    padding-top: 50px;
}

#score-title{
    padding-top: 25px;
    font-size: 30px;
    font-family: 'Aleo', serif;
    color: #FF0000;
    text-align: center;
}
.RPS-Game{
    max-width: 1050px;
    width: 100%;
    margin: 0 auto;
    display: block;
}
#RPS-container{
    display: grid;
    width: 100%;
    grid-template-columns: 3fr 4fr 3fr;
    padding-top: 100px;
}
.RPS-imgs{
    display: block;
    max-width: 280px;
    width: 100%;
    max-height: 280px;
    height: 100%;
    border: 0;
}
.RPS-player{
    margin: 0 22px;
}
.score-div{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.RPS-player-score{
    font-size: 40px;
    font-family: 'Aleo', serif;
}
#RPS-winner{
    font-size: 40px;
    font-family: 'Aleo', serif;
    margin: 170px auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
#RPS-button-container{
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
    margin-bottom: 200px;

}
.RPS-button{
    width: 200px;
    height: 70px;
    border: 0;
    background-color: #00ADF9;
    color: white;
    font-family: 'Aleo', serif;
    font-size: 25px;
    border-radius: 13px;
}
.RPS-button:hover{
    background-color: #4178AA;
    cursor: pointer; 
}
.GGGame-title{
    font-size: 49px;
    font-family: 'Aleo', serif;
    color: #FF0000;
    font-weight: normal;
    text-align: center;
    padding-top: 30px;
}
.GGGame-GuessWord-Container{
    display: flex;
    margin: 0 auto;
    margin-top: 40px;
    
    max-width: -webkit-fit-content;
    
    max-width: -moz-fit-content;
    
    max-width: fit-content;
    width: 100%;
    height: 61px;
}
.GGGame-GuessWord-Character{
    border-bottom: 3px solid black;
    font-size: 50px;
    margin: 0 10px;
    color: #FF0000;
    width: 40px;
    text-align: center; 

}
.GGGame-GuessedWords-Container{
    margin-top: 40px;
    text-align: center; 
    height: 462px;
}
.GGGame-GuessedWords-title{
    font-size: 35px;
    font-family: 'Aleo', serif;
    color: #00ADF9;
    font-weight: normal;
    text-align: center;
}
.GGGame-GuessedWords-Character{
    font-size: 35px;
    font-family: 'Aleo', serif;
    color: #00ADF9;
    
}
.GGGame-input-Container{
    display: flex;
    justify-content: space-between;
    padding: 0 10%;
    margin: 40px 0;
}
.GGGame-button{
    width: 225px;
    height: 91px;
    border: 0;
    background-color: #00ADF9;
    color: white;
    font-family: 'Aleo', serif;
    font-size: 25px;
    border-radius: 20px;
    box-shadow: 1px 3px 6px grey;
}
.GGGame-input{
    border-radius: 20px;
    border: 0;
    box-shadow: 1px 3px 6px grey;
    max-width: 519px;
    width: 100%;
    color: #00ADF9;
    font-family: 'Aleo', serif;
    font-size: 45px;
    text-align: center; 
}
.Hint:hover{
    background-color: #4178AA;
    cursor: pointer; 
}
.AGC-log{
    border: 0;
    border-radius: 20px;
    box-shadow: 1px 3px 6px grey;
    display: block;
    margin: 0 auto;
    resize: none;
    max-width: 600px;
    width: 100%;
    height: 500px;
    padding: 10px;
    font-size: 20px;
    font-family: 'Aleo', serif;
    color: #00ADF9;
    text-align: center;
    white-space: pre-line;
}
.AGC-button-container{
    display: flex;
    margin: 20px 10px;;

}

.AGC-button{
    width: 325px;
    height: 260px;
    border: 0;
    background-color: #00ADF9;
    color: white;
    font-family: 'Aleo', serif;
    font-size: 25px;
    border-radius: 20px;
    box-shadow: 1px 3px 6px grey;
}
.AGC-button:hover{
    background-color: #4178AA;
    cursor: pointer;
}

@media(max-width: 900px){
    body .game.title{
        font-size: 8vw;
        left: 0;
        white-space: normal;
    }
    .computer{
        font-size: 6vw;
    }
    .player{
        font-size: 6vw;
    }
    #score-container{
        width: 80%;
    }
    .GGGame-input-Container{
        padding: 0 1%;
    }
    .GGGame-input{
        font-size: 4vw;
    }
}
@media(max-width: 730px){
    .RPS-player-score{
        font-size: 4vw;
    }
    #RPS-winner{
        font-size: 4vw;
    }
    .RPS-player{
        margin: 0;
    }
    .GGGame-GuessWord-Character{
        font-size: 7vw;
        align-self: flex-end;
    }
}
*{
    margin: 0;
    padding: 0;
}

body{
    background-color: #5AA5E8;
    z-index: 1;
}
#main{
    width: 80%;
    /* height: 100%; */
    background-color: #ffffff;
    margin: 0 auto;
    z-index: 2;

}
#container{
    background-color: rgba(0, 173, 249, .04);
    width: 100%;
    height: 100%;
}
.title{
    font-family: 'Fredoka One', cursive;
    font-weight: normal;
    font-size: 114px;
    text-align: center;
    padding-top: 20px;
    width: 100%;
}
.game.title{
    font-size: 80px;
    position: relative;
    left: 5%;
}
.title-sub{
    font-family: 'Aleo', serif;
    font-weight: bold;
    font-size: 50px;
    color: #707070;
    text-align: center;
    padding-top: 20px;
}
.gameContainer{
    display: grid;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    margin-top: 80px;
    grid-template-rows: repeat(2, minmax(100px, 440px));
    grid-template-columns: repeat(2, minmax(100px, 440px));
    grid-row-gap: 80px;
    grid-column-gap: 80px;
}
.TTT::after{
    background-image: url(/quezGames/static/media/tic_tac_toe.0e73a56f.gif);
}
.RPS::after{
    background-image: url(/quezGames/static/media/rock,_paper_larger.8eb66576.jpg);
}
.GG::after{
    background-image: url(data:image/png;base64,);
}
.AG::after{
    background-image: url(/quezGames/static/media/boombox_background.a72cefff.jpg)
}
.card{
    width:  100%;
    height: auto;
    border-radius: 69px;
    box-shadow: 1px 3px 6px grey;
    position: relative;
}
.card::after{
    content: "";
    border-radius: 69px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: 3;
}
.card:hover:after{
    opacity: 0.7;
    z-index: 40;
    box-shadow: 3px 9px 6px grey;
    cursor: pointer;
}
.card-title{
    text-align: center;
    padding-top: 30px;
    font-size: 50px;
    opacity: 1;
    color: #000000;
    font-family: 'Aleo', serif;
    font-weight: bold;
}
.card-description{
    text-align: center;
    padding: 0px 10px;
    padding-top: 30px;
    font-size: 30px;
    opacity: 1;
    color: #000000;
    font-family: 'Aleo', serif;
    font-weight: normal;
}
.titleBar{
    padding-top: 20px;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
}
.homeButton{
    width: 163px;
    height: 59px;
    background-color: #5AA5E8;
    border: 0;
    border-radius: 24px;
    color: white;
    font-family: 'Aleo', serif;
    font-size: 25px;
    box-shadow: 0px 3px 6px gray;
    z-index: 10;
}
.homeButton:hover{
    background-color: #4178AA;
    cursor: pointer;
}
@media (max-width: 1030px){
    .gameContainer{
        grid-template-rows: repeat(4, minmax(auto, 440px));
        grid-template-columns: repeat(1, minmax(auto, 440px));
    }
    .title{
        font-size: 12vw;
    }
    .title-sub{
        font-size: 5vw;
    }
}
